@import url(https://fonts.googleapis.com/css2?family=Bitter:ital,wght@0,200;0,300;0,400;0,500;0,600;1,200;1,300;1,400;1,500;1,600&display=swap);
html, body {
  position: absolute;
  padding: 0;
  margin: 0;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: hsla(193, 46%, 77%, 1);
  background: linear-gradient(45deg, hsla(193, 46%, 77%, 1) 0%, hsla(153, 42%, 77%, 35%) 45%, hsla(104, 36%, 77%, 1) 100%);
}
* {
  font-family: 'Bitter', serif;
  letter-spacing: 1.2px;
  box-sizing: border-box;
}
@-webkit-keyframes drop-pin-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes drop-pin-rise {
  0% {
    bottom: 0;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.drop-pin-button {
  -webkit-animation: drop-pin-rise 0.5s;
          animation: drop-pin-rise 0.5s;
  z-index: 10;
  cursor: pointer;
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 1.25rem;
  border: 1px solid black;
  background-color: lightgreen;
  box-shadow: 1px 1px 3px gray;
  transition: 0.3s;
}
.drop-pin-button img {
  width: 100%;
}
.drop-pin-backdrop {
  z-index: 7;
  position: fixed;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  background-color: #ccccccaa;
}
.fire-button {
  -webkit-animation: drop-pin-rise 0.5s;
          animation: drop-pin-rise 0.5s;
  z-index: 10;
  cursor: pointer;
  position: fixed;
  right: 1.5rem;
  bottom: 1.5rem;
  height: 2.5rem;
  width: 2.5rem;
  border-radius: 1.25rem;
  border: 1px solid black;
  background-color: rgb(226, 104, 104);
  box-shadow: 1px 1px 3px white;
  transition: 0.3s;
  display: grid;
  align-items: center;
  justify-items: center;
}
.fire-button img {
  width: 1.5rem;
}
.fire-button.one {
  bottom: 4.5rem;
}
.fire-button.two {
  bottom: 7.5rem;
  width: 5rem;
  grid-template-columns: 1fr 1fr;
}
.fire-button.three {
  bottom: 10.5rem;
  width: 7.5rem;
  grid-template-columns: 1fr 1fr 1fr;
}
.fire-button.four {
  bottom: 13.5rem;
  width: 10rem;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}
.fire-button.five {
  bottom: 16.5rem;
  width: 12.5rem;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
}
.drop-pin.place-list {
  display: grid;
  align-items: center;
  grid-template-rows: 1.5fr repeat(6, 1fr);
  grid-gap: 0.5vh;
  gap: 0.5vh;
  margin-top: 0.5vh;
  height: 90vh;
}
.drop-pin.place-list header {
  text-align: center; 
  text-decoration: underline;
}
.drop-pin.place-list h1 {
  color: hsla(120, 73%, 5%, 1);
}
.drop-pin.place {
  transition: 0.3s;
  cursor: pointer;
  display: grid;
  width: 60ch;
  max-width: 84vw;
  margin: auto;
  padding: .25rem 1rem;
  border-radius: 1rem;
  align-items: center;
  background-color: hsla(120, 73%, 15%, 0.7);
  box-shadow: 1px 1px 3px hsla(120, 73%, 15%, 0.4);
  color: white;
}
.drop-pin.place h3 {
  font-weight: 300;
  letter-spacing: 0.5px;
}
@media (hover: hover) {
  .drop-pin-button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    box-shadow: 2px 2px 7px gray;
  }
  .fire-button:hover {
    box-shadow: 2px 2px 7px gray;
  }
  .drop-pin.place:hover {
    background-color: hsla(120, 73%, 15%, 1);
    box-shadow: 2px 2px 5px hsla(120, 73%, 15%, 0.4);
  }
}
@media (hover: none) {
  .drop-pin.place:hover {
    background-color: hsla(120, 73%, 15%, 1);
  }
}
@-webkit-keyframes ol-map-fade-in {
  0% {
    opacity: 0;
  }
}
@keyframes ol-map-fade-in {
  0% {
    opacity: 0;
  }
}
#ol-map {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  -webkit-animation: ol-map-fade-in 2s;
          animation: ol-map-fade-in 2s;
}
#ol-map.hidden {
  visibility: hidden;
}
.auth-conatiner {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-row-gap: 1rem;
  row-gap: 1rem;
  padding: 2rem;
  width: 84vw;
  max-width: 60ch;
  margin: 10vh auto;
  background-color: hsla(0, 0%, 100%, 0.6);
  border-radius: .5rem;
  border-bottom: 0.25rem solid hsla(0, 0%, 100%, 0.1);
  border-right: 0.25rem solid hsla(0, 0%, 100%, 0.1);
  border-top: 0.25rem solid hsla(0, 0%, 100%, 0.8);
  border-left: 0.25rem solid hsla(0, 0%, 100%, 0.8);
}
.auth-conatiner input {
  font-size: 0.95rem;
  height: 1.75rem;
  width: 18ch;
  padding: 0.25rem 2ch;
  outline: none;
  background-color: transparent;
  border: none;
  border-bottom: 0.125rem solid hsla(193, 46%, 77%, 1);
}
.auth-conatiner button {
  cursor: pointer;
  font-size: 0.95rem;
  padding: 0.25rem 2ch;
  margin-block: 0.5rem;
  background-color: hsla(193, 46%, 77%, 0.3);
  border: 0.025rem solid hsla(193, 77%, 5%, 1);
  border-radius: 0.25rem;
}
@media (hover: hover) {
  .auth-conatiner button {
    transition: 0.3s;
  }
  .auth-conatiner button:hover {
    font-weight: 600;
    color: hsla(193, 46%, 77%, 1);
    background-color: hsla(193, 77%, 5%, 1);
  }
}
@media (hover: none) {
  .auth-conatiner button:active {
    font-weight: 600;
    color: hsla(193, 46%, 77%, 1);
    background-color: hsla(193, 77%, 5%, 1);
  }
}

